<template>
    <div>
        <div class="block-toggle my10 py10">
            <div class="py10" v-for="(car, carIndex) in cars">
                <h3 class="block-toggle-title container-columns-max-2 container-grid items-align-center p20 " @click="e => openContent(e)">
                    <div class="title relative pl30">{{ car.name }} | {{ car.gosNumber }}</div>

                    <div class="container-columns2 container-grid items-right items-align-center">
                        <div class="mr10 edit-icon" @click="e => showContentForAddTask(e, 'edit')"></div>
                        <div class="trash-icon" @click="e => removeTask(e, 'cars', index)"></div>
                    </div>
                </h3>

                <div class="new-task-container">
                    <form class="new-task-content my10 p10" data-type="cars" @submit.prevent="saveCars">
                        <input name="id" :value="carIndex" type="text" placeholder="id" class="id hide w-100 p10 mb10 brdr-5">
                        <input name="name" :value="car.name" type="text" placeholder="Название" class="name w-100 p10 mb10 brdr-5">
                        <input name="gosNumber" :value="car.gosNumber" type="text" placeholder="Гос номер" class="gosNumber w-100 p10 mb10 brdr-5">

                        <button type="submit" class="new-task-save p10 w-100">Сохранить</button>
                    </form>
                </div>

                <div class="block-toggle-content">
                    <div v-for="(group, groupIndex) in groups">
                        <h4 class="block-toggle-title container-columns-max-2 container-grid items-align-center pl40 p20" @click="e => openContent(e)">
                            <div class="title relative pl30">{{ group.name }}</div>
                        </h4>

                        <div class="block-toggle-content">
                            <div v-if="service.group == groupIndex" v-for="(service, index) in car.services">
                                <h5 class="block-toggle-title container-columns3-custom container-grid items-align-center p20">
                                  <div class="mr10 check-icon" @click="e => checkProgress(e)"></div>
                                  <div>{{ service.name }}</div>
                                    <div class="container-columns2 container-grid items-right">
                                        <div class="mr10 edit-icon" @click="e => showContentForAddTask(e, 'edit')"></div>
                                        <div class="trash-icon" @click="e => removeTask(e, 'cars.' + carIndex + '.services', index)"></div>
                                    </div>
                                </h5>

                                <div class="mt10 px20 md-font" v-if="service.comment">
                                    <p class="comment p10">Комментарий: {{ service.comment }}</p>
                                </div>

                                <div class="new-task-container px20">
                                    <form class="new-task-content my10" data-type="cars" @submit.prevent="saveCarServices">
                                        <input name="carId" :value="carIndex" type="text" placeholder="carId" class="carId hide w-100 p10 mb10 brdr-5">
                                        <input name="id" :value="index" type="text" placeholder="id" class="id hide w-100 p10 mb10 brdr-5">
                                        <input name="name" :value="service.name" type="text" placeholder="Название" class="name w-100 p10 mb10 brdr-5">
                                        <input name="price" :value="service.price ? service.price : 0" type="text" placeholder="Цена" class="hide price w-100 p10 mb10 brdr-5">
                                        <input name="group" :value="groupIndex" type="text" placeholder="Группа" class="group hide w-100 p10 mb10 brdr-5">
                                        <textarea name="comment" :value="service.comment" placeholder="Комментарий" class="comment w-100 p10 mb10 brdr-5"></textarea>

                                        <button type="submit" class="new-task-save p10 w-100">Сохранить</button>
                                    </form>
                                </div>
                            </div>

                            <div class="new-task-container p20">
                                <div class="new-task-add my10 p10" @click="e => showContentForAddTask(e, 'save')">Добавить услугу</div>

                                <form class="new-task-content my10" data-type="cars.item" @submit.prevent="saveCarServices">
                                    <input name="carId" :value="carIndex" type="text" placeholder="carId" class="carId hide w-100 p10 mb10 brdr-5">
                                    <input name="id" type="text" placeholder="id" class="id hide w-100 p10 mb10 brdr-5">
<!--                                    <p class="comment p10 mb10 md-font">Введите название или выберите из списка</p>-->
                                    <input name="name" type="text" placeholder="Название" class="name w-100 p10 mb10 brdr-5">
                                    <input name="price" type="text" placeholder="Цена" class="hide price w-100 p10 mb10 brdr-5">
                                    <input name="group" :value="groupIndex" type="text" placeholder="Группа" class="group hide w-100 p10 mb10 brdr-5">
<!--                                    <select id="service" class="service p10 mb10 w-100">-->
<!--                                        <option></option>-->
<!--                                        <option :value="servicesIndex" v-for="(servicesItem, servicesIndex) in services.items">{{ servicesItem.name }}</option>-->
<!--                                    </select>-->
                                    <textarea name="comment" placeholder="Комментарий" class="comment w-100 p10 mb10 brdr-5"></textarea>

                                    <button type="submit" class="new-task-save p10 w-100">Сохранить</button>
                                </form>
                            </div>
                        </div>
                    </div>
<!--                    <div class="new-task-add my10 p10" @click="e => showResult(e, 'cars', carIndex)">Подсчитать</div>-->
<!--                    <div class="result hide">Сумма: {{ carSum }}</div>-->
                </div>
            </div>

            <div class="new-task-container mt10 px20">
                <div class="new-task-add p10" @click="e => showContentForAddTask(e, 'save')">Добавить машину</div>

                <form class="new-task-content my10" data-type="cars" @submit.prevent="saveCars">
                    <input name="id" type="text" placeholder="id" class="id hide w-100 p10 mb10 brdr-5">
                    <input name="name" type="text" placeholder="Название" class="name w-100 p10 mb10 brdr-5">
                    <input name="gosNumber" type="text" placeholder="Гос номер" class="gosNumber w-100 p10 mb10 brdr-5">

                    <button type="submit" class="new-task-save p10 w-100">Сохранить</button>
                </form>
            </div>
        </div>
    </div>
</template>

<script>
    export default {
        name: 'Home',
        data() {
            return {
                carSum: 0,
                cars: [],
                groups: [],
                services: {
                    groups: [],
                    items: []
                }
            }
        },
        components: {
        },
        mounted() {
            if (localStorage.getItem('cars')) {
                try {
                    this.cars = JSON.parse(localStorage.getItem('cars'));
                } catch(e) {
                    localStorage.removeItem('cars');
                }
            } else {
                localStorage.cars = "[]";
            }
            if (localStorage.getItem('groups')) {
                try {
                    this.groups = JSON.parse(localStorage.getItem('groups'));
                } catch(e) {
                    localStorage.removeItem('groups');
                }
            } else {
                localStorage.groups = JSON.stringify('[{"name": "Что сделать"}, {"name": "Что купить"}]');
            }
            if (localStorage.getItem('services')) {
                try {
                    this.services = JSON.parse(localStorage.getItem('services'));
                } catch(e) {
                    localStorage.removeItem('services');
                }
            } else {
                localStorage.services = '{"groups":[],"items":[]}';
            }
        },
        methods: {
            removeTask(e, types, id) {
                let storage,
                    type = types.split('.');

                storage = JSON.parse(localStorage[type[0]]);
                if (type.length > 2) {
                    storage[type[1]][type[2]].splice(id, 1);
                } else if (type.length > 1) {
                    storage[type[1]].splice(id, 1);
                } else {
                    storage.splice(id, 1);
                }

                this[type[0]] = storage;
                localStorage.setItem(type[0], JSON.stringify(storage));
            },
            showResult(e, type, id) {
                let storage;

                e.currentTarget.classList.toggle('active');

                this.carSum = 0;
                storage = JSON.parse(localStorage[type]);

                storage[id].services.forEach(function (item) {
                    this.carSum += parseInt(item.price);
                }, this);

                e.currentTarget.parentElement.getElementsByClassName('result')[0].classList.toggle('hide');
            },
            saveCars(e) {
                let parsed,
                    cars = JSON.parse(localStorage.cars);

                var id = e.currentTarget.getElementsByClassName('id').length ? e.currentTarget.getElementsByClassName('id')[0].value : "",
                    name = e.currentTarget.getElementsByClassName('name')[0].value,
                    gosNumber = e.currentTarget.getElementsByClassName('gosNumber')[0].value;

                if (id) {
                    if (cars[id]) {
                        cars[id]['name'] = name;
                        cars[id]['gosNumber'] = gosNumber;
                    }

                    this.cars = cars;
                    parsed = JSON.stringify(cars);
                } else {
                    this.cars.push({
                        name: name,
                        gosNumber: gosNumber
                    });
                    parsed = JSON.stringify(this.cars);
                }

                localStorage.setItem('cars', parsed);
            },
            saveCarServices(e) {
                let parsed,
                    cars = JSON.parse(localStorage.cars);

                var carId = e.currentTarget.getElementsByClassName('carId').length ? e.currentTarget.getElementsByClassName('carId')[0].value : "",
                    id = e.currentTarget.getElementsByClassName('id').length ? e.currentTarget.getElementsByClassName('id')[0].value : "",
                    name = e.currentTarget.getElementsByClassName('name')[0].value,
                    comment = e.currentTarget.getElementsByClassName('comment')[0].value,
                    group = e.currentTarget.getElementsByClassName('group')[0].value,
                    service = e.currentTarget.getElementsByClassName('service')[0],
                    serviceText = service ? service.options[service.selectedIndex].text : "",
                    price = e.currentTarget.getElementsByClassName('price')[0].value
                            ? e.currentTarget.getElementsByClassName('price')[0].value
                            : serviceText
                                    ? this.services.items[service.value].price
                                    : 0;

                if (id) {
                    if (cars[carId].services[id]) {
                        cars[carId].services[id]['name'] = name;
                        cars[carId].services[id]['price'] = price;
                        cars[carId].services[id]['comment'] = comment;
                    }

                    this.cars = cars;
                    parsed = JSON.stringify(cars);
                } else {
                    this.cars[carId].services ? this.cars[carId].services : this.cars[carId].services = [];
                    this.cars[carId].services.push({
                        name: serviceText ? serviceText : name,
                        price: price,
                        comment: comment,
                        group: group
                    });
                    parsed = JSON.stringify(this.cars);
                }

                localStorage.setItem('cars', parsed);
            },
            saveGroups(e) {
                let parsed,
                    groups = JSON.parse(localStorage.groups);

                var id = e.currentTarget.getElementsByClassName('id').length ? e.currentTarget.getElementsByClassName('id')[0].value : "",
                    name = e.currentTarget.getElementsByClassName('name')[0].value;

                if (id) {
                    if (groups[id]) {
                        groups[id]['name'] = name;
                    }

                    this.groups = groups;
                    parsed = JSON.stringify(groups);
                } else {
                    this.groups.push({
                        name: name
                    });
                    parsed = JSON.stringify(this.groups);
                }

                localStorage.setItem('groups', parsed);
            },
            saveServiceGroups(e) {
                let parsed,
                    services = JSON.parse(localStorage.services);

                var id = e.currentTarget.getElementsByClassName('id').length ? e.currentTarget.getElementsByClassName('id')[0].value : "",
                        name = e.currentTarget.getElementsByClassName('name')[0].value;

                if (id) {
                    if (services && services.groups[id]) {
                        services.groups[id]['name'] = name;
                    }

                    this.services.groups = services.groups;
                    parsed = JSON.stringify(services);
                } else {
                    this.services.groups.push({
                        name: name
                    });

                    parsed = JSON.stringify(this.services);
                }

                localStorage.setItem('services', parsed);
            },
            saveServiceItems(e) {
                let parsed,
                    services = JSON.parse(localStorage.services);

                var id = e.currentTarget.getElementsByClassName('id').length
                        ? e.currentTarget.getElementsByClassName('id')[0].value
                        : "",
                        name = e.currentTarget.getElementsByClassName('name')[0].value,
                        price = e.currentTarget.getElementsByClassName('price')[0].value,
                        group = e.currentTarget.getElementsByClassName('group')[0].value;

                if (id) {
                    if (services && services.items[id]) {
                        services.items[id]['name'] = name;
                        services.items[id]['price'] = price;
                        services.items[id]['group'] = group;
                    }

                    parsed = JSON.stringify(services);
                } else {
                    this.services.items.push({
                        name: name,
                        price: price,
                        group: group
                    });

                    parsed = JSON.stringify(this.services);
                }

                localStorage.setItem('services', parsed);
            },
            openContent (e) {
                e.currentTarget.classList.toggle('active');
                e.currentTarget.parentElement.getElementsByClassName('block-toggle-content')[0].classList.toggle('active');
            },
            showContentForAddTask (e, type) {
                e.currentTarget.classList.toggle('active');

                if (type == 'save') {
                    e.currentTarget.parentElement.getElementsByClassName('new-task-content')[0].classList.toggle('show');
                } else if (type == 'edit') {
                    e.currentTarget.parentElement.parentElement.parentElement.getElementsByClassName('new-task-content')[0].classList.toggle('show');
                }
            },
            checkProgress (e) {
                e.currentTarget.classList.toggle('active');
            }
        }
    }
</script>

<style lang="scss" scoped>
    @import '../style/helpers/_variables';
</style>



