import Vue from 'vue'
import Router from 'vue-router'
import Home from '@/components/Home.vue'
import Admin from '@/components/Admin.vue'

const routes = [
  { path: '/', component: Home, name: 'Home' },
  { path: '/admin', component: Admin, name: 'Admin' }
]

Vue.use(Router)

export default new Router({
  routes
})



