<template>
    <div>
        {{children}}
        <div class="py10" v-for="(category, index) in categories">
            <h4 class="block-toggle-title container-columns-max-2 container-grid items-align-center pl40 p20" @click="e => openContent(e)">
                <div class="title relative pl30">{{ category.name }}</div>

    <!--            <div class="container-columns2 container-grid">-->
    <!--                <div class="new-task-add mr10 p10" @click="e => showContentForAddTask(e, 'edit')">Редактировать</div>-->
    <!--                <div class="new-task-add p10" @click="e => removeTask(e, 'services.groups', index)">Удалить</div>-->
    <!--            </div>-->
            </h4>

            <div class="block-toggle-content px10" v-if="category.items">
                <div class="py10">
                    <categories :children="category.items"></categories>
                </div>
            </div>
        </div>
    </div>
</template>

<!--<div class="new-task-container">-->
<!--    <form class="new-task-content my10 p10" data-type="groups" @submit.prevent="saveServiceGroups">-->
<!--        <input name="id" :value="index" type="text" placeholder="id" class="id hide w-100 p10 mb10 brdr-5">-->
<!--        <input name="name" :value="group.name" type="text" placeholder="Название" class="name w-100 p10 mb10 brdr-5">-->

<!--        <button type="submit" class="new-task-save p10 w-100">Сохранить</button>-->
<!--    </form>-->
<!--</div>-->

<script>
import Categories from "../../components/Block/Category.vue";

export default {
  name: 'Category',
  props: [
    'categories',
    'children'
  ],
  components: {
    'categories': Categories
  },
  methods: {
    openContent (e) {
      e.currentTarget.classList.toggle('active');
      e.currentTarget.parentElement.getElementsByClassName('block-toggle-content')[0].classList.toggle('active');
    },
    showContentForAddTask (e, type) {
      e.currentTarget.classList.toggle('active');

      if (type == 'save') {
        e.currentTarget.parentElement.getElementsByClassName('new-task-content')[0].classList.toggle('show');
      } else if (type == 'edit') {
        e.currentTarget.parentElement.parentElement.parentElement.getElementsByClassName('new-task-content')[0].classList.toggle('show');
      }
    }
  }
}
</script>



